import gsap from "gsap";
import scrollInit from "./scroll-reveal";

function renderPage() {
  const render = $(".render");
  gsap
    .fromTo(
      render,
      0.5,
      { opacity: "1", zIndex: "99999" },
      { opacity: "0", zIndex: "-1", onComplete: () => {
        render.remove();
        scrollInit();
      }}
    )
    .delay(0.4);
}
renderPage();